import React, { useCallback } from "react";

import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { emptyCart } from "../../redux/cartSlice";

import { HOME_PAGE } from "../../helpers";
import { deleteAllCartItems } from "../../http";

import { ButtonsContainer, STable } from "./CartTable.styled";
import CartItem from "./CartItem";
import Button from "../core-ui/button/Button";
import { selectCart } from "../../redux/selectors";
import TableHead from "./TableHead";

const CartTable = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const cart = useSelector(selectCart);

  const navigateHandler = useCallback(() => {
    navigate(HOME_PAGE);
  }, [navigate]);

  const emptyCartHandler = useCallback(async () => {
    deleteAllCartItems(cart).then((json) => {
      console.log(json);
      dispatch(emptyCart());
    });
  }, [dispatch, cart]);

  return (
    <div>
      <STable>
        <TableHead />
        <tbody>
          {cart.map(({ product, count, id }, index) => (
            <CartItem
              key={id}
              item={product}
              count={count}
              id={id}
              index={index}
            />
          ))}
        </tbody>
      </STable>
      <ButtonsContainer>
        <Button
          onClick={navigateHandler}
          color='orange'>
          Continue shopping
        </Button>
        <Button
          outlined
          color='grey'>
          Update cart
        </Button>
        <Button
          outlined
          color='red'
          onClick={emptyCartHandler}>
          Clear cart
        </Button>
      </ButtonsContainer>
    </div>
  );
};

export default CartTable;
