import React from "react";
import { useMedia } from "../../hooks/useMedia";

import { SThead } from "./CartTable.styled";

const TableHead = () => {
  const isMobile = useMedia("mobileL");

  return (
    isMobile && (
      <SThead>
        <tr>
          <th>Product</th>
          <th>Price</th>
          <th>Quantity</th>
          <th>Subtotal</th>
          <th />
        </tr>
      </SThead>
    )
  );
};

export default TableHead;
